.social-layer {
  display: inline-block;
  text-align: center;
  width: 100%;
  color: lighten(gray, 30%);
  font-size: 0.9em;

  .social-icons {
    ul {
     list-style: none;
      li {
        display: inline;
      }
    }
  }
}

.fixed-footer {
  position: fixed;
  left: 0;
  bottom: 15px;
  text-align: center;
  font-size: 0.9em;
  width: 100%;
}

